.context {
    width: 100%;
    position: absolute;
    padding:10px 10px 10px;
    
}
.context .container
{
  justify-content: center;
  display: flex;
  align-items: center;
  max-width: 1200px;
  flex-wrap: wrap;
  padding:40px 0px;
}
.context .container .card
{
 position:relative;
 min-width: 320px;
 height:390px;
 box-shadow: inset 5px 5px 5px rgba(0,0,0,0.5),
             inset -5px -5px 5px rgba(255,255,255,0.5),
             -5px -5px 5px rgba(255,255,255,0.5),
             -5px -5px 5px rgba(255,255,255,0.5);
border-radius:15px;     
margin:30px;        

}
.context .container .card .box
{
  position:absolute;
  top:20px;
  left:20px;
  right:20px;
  bottom:20px;
  box-shadow:0 10px 20px rgba(0,0,0,0.1);
  border-radius:15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.context .container .card .box .content
{
  padding: 20px;
  text-align:center;
}

.context .container .card .box .content h2
{
  font-size:1.8em;
  color:black;
  z-index:1;
  transition:0.5s;
}
.context .container .card:hover .box
{
 -webkit-transform: translateY(-50px);
         transform: translateY(-50px);
 box-shadow: 0 10px 40px rgba(0,0,0,0,0.4);
 background: #fffdd0;
}
.context .container .card:hover .box h2
{
  color:#FFD700;
}
.context .container .card:hover .box p
{
  color:#fff;
}

.context .container .card .box .content p
{
  font-size:1em;
  font-weight:300;
  color:#666666;
  z-index:1;
  transition:0.5s;
}





